.advantages-card {
  max-width: 360px;
  min-height: 241px;
  background-color: $color-alice-blue;
  padding: 30px;
}

.advantages-card h3 {
  color: $color-tangaroa;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  margin-top: 12px;
  margin-bottom: 9px;
}

.advantages-card p {
  color: $color-tangaroa;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  margin: 0;
}

@media (max-width: $vp-tablet) {
  .advantages-card {
    max-width: 206px;
    min-height: 310px;
    padding: 26px 16px 25px 20px;
  }

  .advantages-card__img {
    width: auto;
    height: 26px;
  }
}


@media (max-width: $vp-mobile) {}
